
.header{
    justify-content:center;
    align-items: center;
    background-size: cover;
    background-image: url(./galerie/backg.jpg);
  }

  h1 {
    font-size: 40px;
     text-transform: uppercase;
  }

  h3{
    font-size: 35px;
  }
  
  .hero-title{
    text-shadow: 2px 2px 8px #000000;

  }
  .header-hero{
    display:flex;
    justify-content:center;
    align-items: center;
    text-align: center;
    height: 300px;
    color:#fff
  }

  .nav-pills ul{
    gap: 0;
    padding: 0;
  }
  
  .nav-pills li{
    font-size: 25px;
    text-shadow: 2px 2px 8px #000000;
    display: block;
    text-align: center;
    padding: 14px 16px;
    color:#fff
  }

  
  
  .nav-pills li:hover {
   color:aqua;
   transition: 0.5s;
  }

  ul {
    display: flex;
    gap: 16px;
    list-style-type: none;
    justify-content: center;
  }