

.projt-row{
    padding: 5px;
    display: flex;;
    justify-content: space-between;
    gap: 16px;
    height: 100%;
   
  }
  
  .projt-wrap{
    display: block;
    max-height: 500px;
    width: 100%;
    height: 80%;
    border-radius: 15px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px; 
    padding: 0;;
  }

  .projet-img-cont{
    height: 300px;
  }
  
  .projet-img{
    border-top-left-radius: 15px;
    border-top-right-radius:15px;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .descr-cont{
    display: flex;
    align-items: center;
    justify-content: center;
    padding:10px;
    margin: 0;
  }

.descr a{
    text-decoration: 'none';
  }
  
  .portfolio-cont{
    height: 100%;
    padding: 50px;
    gap: 16px;
  }